<template>
  <div id="app">
    <small>Feito por Clarice Lira e Leonardo Lira</small>
    <h1>Jogo da mesada</h1>   
    <div v-if="!iniciado">
      <center>
      <form>
        <div class="form-group col-md-6 ">
          <label for="exampleInputEmail1" class="justify-content-start">Número de operações</label>
          <input type="number" class="form-control col-md-2" v-model="numOperacoes">          
        </div>        
        <button type="button" class="btn btn-primary" v-on:click="iniciarJogo()">Iniciar Rodada</button>
      </form>
      </center>

    </div>
    <div v-if="iniciado">
      <center>
      <table role="table" width="60%" aria-busy="false" aria-colcount="4" class="table b-table table-striped table-hover pl-5 pr-5">
        <tbody role="rowgroup">
          <tr v-for="dado in dados" :key="dado.id" role="row" :class="dado.erro?'table-danger':''">
            <td aria-colindex="1" role="cell" width="30%" class="coluna"><img width="80%" alt="Vue logo" :src="getImgUrl(dado.cedula1)"></td>
            <td aria-colindex="2" role="cell" width="5%" style="font-size: 2em" class="coluna">{{dado.operacao}}</td>
            <td aria-colindex="3" role="cell" width="30%" class="coluna"><img width="80%" alt="Vue logo" :src="getImgUrl(dado.cedula2)"></td>          
            <td aria-colindex="4" role="cell" width="5%" style="font-size: 2em" class="coluna">=</td>
            <td aria-colindex="5" role="cell" width="30%" class="coluna">
              <span class="form-group col-md-12 ">
                <input type="number" class="col-md-8" id="" v-model="dado.resultado">
                <span class="col-md-2">
                  <b-icon v-if="dado.validado=='SIM'" icon="hand-thumbs-up" variant="success"></b-icon>
                  <b-icon v-if="dado.validado=='NAO'" icon="hand-thumbs-down" variant="danger"></b-icon>
                </span>
              </span>
            </td>
          </tr>
        </tbody>
      </table>
      </center>
      <button type="button" class="btn btn-danger mr-2 " v-on:click="cancelar()">Cancelar Rodada</button>
      <button type="button" class="btn btn-primary" v-on:click="validar()">Validar Rodada</button>
    </div> 
    
  </div>
</template>

<script>

export default {
  name: 'App',
  data () {
    return {
      dados: [],
      iniciado: false,
      numOperacoes: 2
    }
  },
  methods: {
    getImgUrl(cedula) {
      var images = require.context('./assets/', false, /\.jpg$/)
      return images('./' + cedula + ".jpg")
    },
    iniciarJogo() {
      this.iniciado = true
      //debugger; // eslint-disable-line no-debugger
      let cedulas = ['1', '2', '5', '10', '20', '50', '100', '200']
      let operacoes = ['+', '-']           
      
      for (let index = 0; index < this.numOperacoes; index++) {
        let operacao = operacoes[Math.floor(Math.random() * operacoes.length)]
        let cedula1 = cedulas[Math.floor(Math.random() * cedulas.length)]
        while (operacao === '-' && cedula1 === '1') {
          cedula1 = cedulas[Math.floor(Math.random() * cedulas.length)]
        }
        let cedula2 = cedulas[Math.floor(Math.random() * cedulas.length)]
        while (operacao === '-' && cedula2 > cedula1) {
          cedula2 = cedulas[Math.floor(Math.random() * cedulas.length)]
        }
        this.dados.push({
          id: index,
          cedula1: cedula1,
          cedula2: cedula2,
          operacao: operacao,
          resultado: '',
          validado: '',
          erro: false
        })
      }
    },  
    cancelar() {
      this.iniciado = false
      this.dados = []
    },
    validar() {
      this.dados.forEach(dado => {
        if (dado.resultado === '') {
          dado.erro = true
        } else {
          dado.erro = false
          if (dado.operacao === '+') {
            let res = parseInt(dado.cedula1) + parseInt(dado.cedula2)
            if (res === parseInt(dado.resultado)) {
              dado.validado = 'SIM'
            } else {
              dado.validado = 'NAO'
            }
          } else {
             let res = parseInt(dado.cedula1) - parseInt(dado.cedula2)
            if (res === parseInt(dado.resultado)) {
              dado.validado = 'SIM'
            } else {
              dado.validado = 'NAO'
            }
          }
        }
      });
    }
    
  },
  beforeMount(){
      this.iniciado = false;
    }  
}
</script>

